import { NetworkServiceProvider } from './../../providers/network-service/network-service';
import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, MenuController, Scroll, Content } from 'ionic-angular';
import { BasePage } from '../base/base';
import { AppServiceProvider, AppGlobal } from '../../providers/app-service/app-service';

/**
 * Generated class for the HandleMonitorPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
let handleThis;
declare const $;
@IonicPage()
@Component({
  selector: 'page-handle-monitor',
  templateUrl: 'handle-monitor.html',
})
export class HandleMonitorPage extends BasePage {
  @ViewChild("handleContent") handleContent: Content;
  @ViewChild("headerHandle") headerHandle: Scroll;
  @ViewChild("leftHandle") leftHandle: Scroll;
  @ViewChild("rightHandle") rightHandle: Scroll;
  arrDate: string = "minute";
  minuteDate: minuteDate = new minuteDate;
  trees: any;
  treeId: any = '';
  handleData: any = [];
  dataType: any = '1';
  pageNum: any = 0;
  pageSize: any = 30;
  style: style = new style;
  displayFormat: any;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    public menuCtrl: MenuController,
    public net: NetworkServiceProvider) {
    super(navCtrl, navParams, toastCtrl)
    handleThis = this;
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad HandleMonitorPage');
    this.time();
    this.scroll();
    this.getHandleData();
    this.getSiteTree();
  }
  query() {
    this.menuCtrl.enable(true, "handleMenu");
    this.menuCtrl.toggle("handleMenu");
  }
  determine() {
    this.handleData = [];
    this.getHandleData();
    this.menuCtrl.enable(true, "handleMenu");
    this.menuCtrl.toggle("handleMenu");
  }
  time() {
    if (this.arrDate == "minute") {
      this.displayFormat = "YYYY-MM-DD hh:mm";
      this.minuteDate.startDate = new Date(new Date().getTime() + 7 * 60 * 60 * 1000).toISOString();
      this.minuteDate.endDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
    } else if (this.arrDate == "hour") {
      this.displayFormat = "YYYY-MM-DD hh";
      this.minuteDate.startDate = new Date(new Date().getTime() + 5 * 60 * 60 * 1000).toISOString();
      this.minuteDate.endDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
    } else if (this.arrDate == "day") {
      this.displayFormat = "YYYY-MM-DD";
      this.minuteDate.startDate = new Date(new Date().getTime() - 4 * 16 * 60 * 60 * 1000).toISOString();
      this.minuteDate.endDate = new Date(new Date().getTime() + 8 * 60 * 60 * 1000).toISOString();
    }
  }
  getSiteTree() {
    let param: any = {};
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getSiteTree,
      param, res => {
        console.log(res);
        this.trees = res;
        this.tree();
      }, error => {
        this.toast(error);
      }
    )
  }
  tree() {
    $('#jsTreeHandle').jstree({
      'core': {
        "themes": {
          "dots": false // no connecting dots between dots
        },
        'data': this.trees["污水处理厂"]
      },
      "plugins": ["search"]
    });
    $('#jsTreeHandle').jstree().open_all();
    $('#jsTreeHandle').on("changed.jstree", (event, data) => {
      var str = data.node.id;
      if (str.slice(0, 1) != "-") {
        this.treeId = str;
      } else {
        this.treeId = '';
      }
      console.log(this.treeId)
    });
  }

  scrollStyle() {
    let handleContent = $("#handleContent").height();
    let handleHeader = $("#handleHeader").height();
    let handleHead = $("#handleHead").height();
    let handleStationNames = $("#handleStationNames").height();
    let handleHeight = handleContent - handleHeader - handleHead - 30;
    if (handleStationNames < handleHeight) {
      this.style.handleHeight = handleStationNames + 'px';
    } else {
      this.style.handleHeight = handleContent - handleHeader - handleHead - 30 + 'px';
    }
  }
  scroll() {
    this.rightHandle.addScrollEventListener(function (event: any) {
      handleThis.headerHandle._scrollContent.nativeElement.scrollLeft = event.target.scrollLeft;
      handleThis.leftHandle._scrollContent.nativeElement.scrollTop = event.target.scrollTop;
    });
  }
  getHandleData() {
    let startDate;
    let endDate;
    if (this.arrDate == "minute") {
      this.dataType = '1';
      startDate = this.minuteDate.startDate.replace("T", " ").replace("Z", "").substring(0, 16) + ':00';
      endDate = this.minuteDate.endDate.replace("T", " ").replace("Z", "").substring(0, 16) + ':00';
    } else if (this.arrDate == "hour") {
      startDate = this.minuteDate.startDate.replace("T", " ").replace("Z", "").substring(0, 13) + ':00:00';
      endDate = this.minuteDate.endDate.replace("T", " ").replace("Z", "").substring(0, 13) + ':00:00';
      this.dataType = '2';
    } else if (this.arrDate == "day") {
      startDate = this.minuteDate.startDate.replace("T", " ").replace("Z", "").substring(0, 10) + ' 00:00:00';
      endDate = this.minuteDate.endDate.replace("T", " ").replace("Z", "").substring(0, 10) + ' 00:00:00';
      this.dataType = '3';
    }
    let param: any = {};
    param.siteId = this.treeId;
    param.startTime = startDate;
    param.endTime = endDate;
    param.dataType = this.dataType;
    param.pageNum = this.pageNum;
    param.pageSize = this.pageSize;
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getPolluteWaterData,
      param, res => {
        if (res) {
          for (let handleData of res) {
            this.handleData.push(handleData);
          }
        }

        console.log(this.handleData);
        setTimeout(() => {
          this.scrollStyle();
        }, 10)
      }, error => {
        this.toast(error);
      }
    )
  }
  loadMore() {
    this.pageNum = this.handleData.length;
    this.getHandleData();
  }
  monitor(item){
    item.siteType='4';
    this.navCtrl.push('HandleDetailsPage', { site: item, id: '1' });
  }
}
class minuteDate {
  maxDate: string = "";
  startDate: string = "";
  endDate: string = "";
}
class style {
  handleSiteName: string = '';
  handleEquipmentName: string = '';
  handleRecordingTime: string = '';
  handleBo1: string = '';
  handlePh: string = '';
  handlePb: string = '';
  handleCd: string = '';
  handleShen: string = '';
  handleZn: string = '';
  handleCu: string = '';
  handleHeight: string = '';
}



